header-outer-container {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center; 
    /* background-color: rgb(148, 132, 132); */
    background-color: rgb(20, 20, 20);
    background-color: transparent; 
    height: 10vh;
    color: white;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index: 1;
   
    
  }
  .nav_black {
    background-color: rgb(20, 20, 20);
  }
  /* Header container */
  .header-container {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
  
  }
  /* Header Left */
  
  .header-left ul{
      width: 140%;
      list-style: none;
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
  }
  /* Header right */
  .header-right ul{
  gap: 15px;
  list-style: none;
  display: flex;
  justify-content:space-between;
  align-items: center;
  }